import { useState } from "react";
import { styled } from "styled-components";
import { BsCheckCircleFill } from "react-icons/bs";

/*
    id:  ,
    todo: input value,
    done: checkbox (boolean),
    mode:  (boolean)
*/
const TodoForm = ({
    toDoId,
    setTodoId,
    setTodoList,
    writeModeColor,
    setWriteMode,
}) => {
    const [todo, setTodo] = useState("");
    const onChange = (e) => {
        setTodo(e.target.value);
    };

    const onSubmit = (e) => {
        e.preventDefault();
        // setTodoList([...toDoList, todo]);
        //   : (f12-perfomence)      .
        setTodoList((prev) => [
            {
                id: toDoId, // Ref   .
                todo: todo,
                done: false,
                mode: false,
                noteColor: writeModeColor,
            },
            ...prev,
        ]);
        setTodo("");
        setWriteMode(false);
        setTodoId((prev) => prev + 1); // id   . ( todo  )
    };

    return (
        <TextBox onSubmit={onSubmit}>
            <TextArea
                value={todo}
                onChange={onChange}
                placeholder="  "
                bgcolor={writeModeColor}
            />
            <button type="submit" className="btn_edit">
                <BsCheckCircleFill className="icon" />
            </button>
        </TextBox>
    );
};

export default TodoForm;

const TextBox = styled.form`
    position: relative;
    .btn_edit {
        position: absolute;
        right: 16px;
        bottom: 16px;
        .icon {
            font-size: 24px;
        }
    }
`;
const TextArea = styled.textarea`
    padding: 16px;
    width: 200px;
    height: 200px;
    background-color: ${(props) => props.bgcolor};
    border-radius: 16px;
`;
